import React, { useState } from "react";
import "./index.css";
import { IoArrowBack } from "react-icons/io5";
import { Link } from "react-router-dom";

function Attendence() {
  const [imageFile, setImageFile] = useState(null);
  const [imagePreview, setImagePreview] = useState(null);
  const [isUploading, setIsUploading] = useState(false);

  const handleImageChange = (e) => {
    const file = e.target.files[0];
    setImageFile(file);
    if (file) {
      setImagePreview(URL.createObjectURL(file));
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!imageFile) {
      alert("Please select an image first.");
      return;
    }

    const formData = new FormData();
    formData.append("image", imageFile);

    try {
      setIsUploading(true);
      const res = await fetch("/api/attendence-image", {
        method: "POST",
        body: formData,
      });

      if (res.ok) {
        const blob = await res.blob();
        const url = window.URL.createObjectURL(blob);
        const a = document.createElement("a");
        a.href = url;
        a.download = "attendance.xlsx";
        document.body.appendChild(a);
        a.click();
        a.remove();
        window.URL.revokeObjectURL(url);
      } else {
        const error = await res.text();
        console.error(error);
        alert("Failed to generate report.");
      }
    } catch (err) {
      console.error("Error uploading image:", err);
      alert("An error occurred.");
    } finally {
      setIsUploading(false);
    }
  };

  return (
    <div className="registerFace">
      <Link to="/">
        <button
          style={{
            position: "absolute",
            top: "10px",
            left: "10px",
            background: "transparent",
          }}
        >
          <IoArrowBack size="30" />
        </button>
      </Link>

      <h2>Generate Attendance and Activity Monitoring Report</h2>
      <div className="mkl">
        <form className="register-face-card" onSubmit={handleSubmit}>
          <input
            type="file"
            accept="image/*"
            onChange={handleImageChange}
            required
            disabled={isUploading}
            name="image"
          />
          {imagePreview && (
            <img
              src={imagePreview}
              alt="Preview"
              width="100%"
              style={{ marginTop: "10px", borderRadius: "10px" }}
            />
          )}
          <button type="submit" disabled={isUploading || !imageFile}>
            {isUploading ? (
              <>
                <span className="loading-icon"></span> Uploading...
              </>
            ) : (
              "Upload"
            )}
          </button>
        </form>
      </div>
    </div>
  );
}

export default Attendence;
