import React, { useState, useRef } from "react";
import "./index.css";
import { Link } from "react-router-dom";
import { IoArrowBack } from "react-icons/io5";
function RegisterFace() {
  const [studentInfo, setStudentInfo] = useState({
    name: "",
    rollno: "",
  });

  const [images, setImages] = useState([]);
  const [isLoading, setIsLoading] = useState(false); // State to handle loading status
  const [isDisabled, setIsDisabled] = useState(false); // State to disable form inputs
  const [webcamStream, setWebcamStream] = useState(null); // To hold webcam stream
  const videoRef = useRef(null); // Reference for video element
  const canvasRef = useRef(null); // Reference for canvas element

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setStudentInfo((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleFileChange = (e) => {
    setImages((prevImages) => [...prevImages, ...e.target.files]);
  };

  // Start the webcam feed
  const startWebcam = async () => {
    try {
      const stream = await navigator.mediaDevices.getUserMedia({
        video: true,
      });
      setWebcamStream(stream);
      videoRef.current.srcObject = stream;
    } catch (err) {
      console.error("Error accessing webcam:", err);
    }
  };

  // Capture image from webcam
  const captureWebcamImage = () => {
    const canvas = canvasRef.current;
    const context = canvas.getContext("2d");
    context.drawImage(videoRef.current, 0, 0, canvas.width, canvas.height);
    canvas.toBlob((blob) => {
      const file = new File([blob], "webcam_image.jpg", { type: "image/jpeg" });
      setImages((prevImages) => [...prevImages, file]);
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    setIsLoading(true);
    setIsDisabled(true); // Disable inputs and button

    const formData = new FormData();
    formData.append("name", studentInfo.name);
    formData.append("rollno", studentInfo.rollno);

    for (let i = 0; i < images.length; i++) {
      formData.append("images[]", images[i]);
    }

    try {
      const res = await fetch("/api/register-face", {
        method: "POST",
        body: formData,
      });

      if (res.ok) {
        alert("Upload successful!");
      } else {
        alert("Upload failed.");
        const error = await res.text();
        console.log(error);
      }
      console.log("images", images);
    } catch (err) {
      console.error("Error uploading:", err);
    } finally {
      setIsLoading(false);
      setIsDisabled(false); // Re-enable inputs and button
    }
  };

  return (
    <div className="registerFace">
      <Link to="/">
        <button
          style={{
            position: "absolute",
            top: "10px",
            left: "10px",
            background: "transparent",
          }}
        >
          <IoArrowBack size="30" />
        </button>
      </Link>
      <h2>Register new Candidate Face</h2>
      <div className="mkl">
        <form className="register-face-card" onSubmit={handleSubmit}>
          <div className="iol">
            <label>Name</label>
            <input
              name="name"
              value={studentInfo.name}
              onChange={handleInputChange}
              required
              disabled={isDisabled} // Disable input field if form is submitting
            />
          </div>
          <div className="iol">
            <label>Roll Number</label>
            <input
              name="rollno"
              value={studentInfo.rollno}
              onChange={handleInputChange}
              required
              disabled={isDisabled} // Disable input field if form is submitting
            />
          </div>

          {/* File input for manual upload */}
          <input
            type="file"
            name="images[]"
            multiple
            accept="image/*"
            onChange={handleFileChange}
            required
            disabled={isDisabled} // Disable file picker if form is submitting
          />

          {/* Webcam feed */}
          <div>
            <button type="button" onClick={startWebcam} disabled={isDisabled}>
              Start Webcam
            </button>
            <video
              ref={videoRef}
              width="320"
              height="240"
              autoPlay
              style={{ border: "1px solid black" }}
            />
            <canvas
              ref={canvasRef}
              width="320"
              height="240"
              style={{ display: "none" }}
            />
            <button
              type="button"
              onClick={captureWebcamImage}
              disabled={isDisabled}
            >
              Capture from Webcam
            </button>
          </div>

          <button type="submit" disabled={isDisabled}>
            {isLoading ? (
              <>
                <span className="loading-icon"></span> Processing...
              </>
            ) : (
              "Upload"
            )}
          </button>
        </form>
      </div>
    </div>
  );
}

export default RegisterFace;
