import React, { useState } from "react";
import "./index.css";
import { Link } from "react-router-dom";
import { IoArrowBack } from "react-icons/io5";

const GenerateReport = () => {
  const [videoFile, setVideoFile] = useState(null);
  const [videoPreview, setVideoPreview] = useState(null);
  const [isUploading, setIsUploading] = useState(false);

  const handleVideoChange = (e) => {
    const file = e.target.files[0];
    setVideoFile(file);
    if (file) {
      setVideoPreview(URL.createObjectURL(file));
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!videoFile) {
      alert("Please select a video first.");
      return;
    }

    const formData = new FormData();
    formData.append("video", videoFile);

    try {
      setIsUploading(true);
      const res = await fetch("/api/generate-report", {
        method: "POST",
        body: formData,
      });

      if (res.ok) {
        const blob = await res.blob();
        const url = window.URL.createObjectURL(blob);
        const a = document.createElement("a");
        a.href = url;
        a.download = "attendance_report.xlsx"; // filename
        document.body.appendChild(a);
        a.click();
        a.remove();
        window.URL.revokeObjectURL(url);
      } else {
        const error = await res.text();
        console.error(error);
        alert("Failed to generate report.");
      }
    } catch (err) {
      console.error("Error uploading video:", err);
      alert("An error occurred.");
    } finally {
      setIsUploading(false);
    }
  };

  return (
    <div className="registerFace">
      <Link to="/">
        <button
          style={{
            position: "absolute",
            top: "10px",
            left: "10px",
            background: "transparent",
          }}
        >
          <IoArrowBack size="30" />
        </button>
      </Link>
      <h2>Generate Attendance and Activity Monitoring Report</h2>
      <div className="mkl">
        <form className="register-face-card" onSubmit={handleSubmit}>
          <input
            type="file"
            accept="video/*"
            onChange={handleVideoChange}
            required
            disabled={isUploading}
            name="video"
          />
          {videoPreview && (
            <video
              width="100%"
              height="auto"
              controls
              src={videoPreview}
              style={{ marginTop: "10px", borderRadius: "10px" }}
            />
          )}
          <button type="submit" disabled={isUploading || !videoFile}>
            {isUploading ? (
              <>
                <span className="loading-icon"></span> Uploading...
              </>
            ) : (
              "Upload"
            )}
          </button>
        </form>
      </div>
    </div>
  );
};

export default GenerateReport;
