import React from "react";
import { Link } from "react-router-dom";
import "./index.css";
import Cookies from "js-cookie";
import { useNavigate } from "react-router-dom";
function Home() {
  const navigate = useNavigate();
  const LogOut = () => {
    Cookies.remove("FACERECID");
    navigate("/login");
  };
  return (
    <div className="home">
      <button
        onClick={LogOut}
        style={{
          position: "absolute",
          top: "10px",
          right: "10px",
          background: "white",
        }}
      >
        Logout
      </button>
      <h2>Welcome to Face Recognition and activity monitoring</h2>
      <div className="button-group">
        <Link to="/register-face">
          <button className="btn btn-primary mjk mjkj">
            Register Candidate Face
          </button>
        </Link>
        <Link to="/generate-report">
          <button className="btn btn-primary mjk mjkj">
            Generate Attendence Report
          </button>
        </Link>
        <Link to="/attendence">
          <button className="btn btn-primary mjk mjkj">
            Attendence From Image
          </button>
        </Link>
        {/* <Link to="/activity-monitoring">
          <button className="btn btn-primary mjk">Activity Monitoring</button>
        </Link> */}
      </div>
    </div>
  );
}

export default Home;
